<template>
  <section>
    <div class="container">
      <h2 class="title">{{this.model}} model</h2>

      <ul class="tab-group ul">
        <li
          v-for="(item, index) in itemGroup"
          :key="index"
          class="tab-list"
          v-bind:class="[item.idx==activeTab ? 'active' : '']"
          v-on:click="fn_tab(item.idx)"
        >{{ item.title }}</li>
      </ul>

      <select
        v-model="selected"
        @change="fn_select"
        class="tab-group select"
        :style="{'background-image': 'url(' + require('@/assets/icons/down_white.png') + ')'}"
      >
        <option
          v-for="(item, index) in itemGroup"
          :key="index"
          v-bind:value="item.idx"
          v-bind:selected="[item.idx==activeTab ? 'true' : '']"
        >{{item.title}}</option>
      </select>

      <div class="layout-grid list-group">
        <button
          v-for="(item, index) in itemFilter"
          :key="index"
          class="layout-grid__cell column-2 list"
          v-on:click="fn_link(item.idx)"
        >
          <span v-text="item.title" class="list-title">XXXXXX</span>
        </button>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      selected: 0,
      activeTab: 0,
      group: this.$route.params.group,
      model: this.$route.params.model,
      itemGroup: [
        { idx: 0, title: "D-I 표준" },
        { idx: 1, title: "D-I 표준 + D-Troll" },
        { idx: 2, title: "Doosan 공급용" },
        { idx: 3, title: "Mitsubishi 공급용" },
        { idx: 4, title: "Baudouin 공급용" },
        { idx: 5, title: "Weichai 공급용" }
      ],
      items: [
        {
          idx: 0,
          groupIdx: 0,
          title: "Input Shaft Group"
        },
        {
          idx: 1,
          groupIdx: 0,
          title: "Asterm Shaft Group"
        },
        {
          idx: 2,
          groupIdx: 1,
          title: "Output Shaft Group"
        },
        {
          idx: 3,
          groupIdx: 1,
          title: "Pump holder Group"
        },
        {
          idx: 4,
          groupIdx: 1,
          title: "Valve holder Group"
        },
        {
          idx: 5,
          groupIdx: 2,
          title: "Case Group"
        }
      ],
      itemFilter: []
    };
  },
  created() {
    this.$store.commit("oderStep", 1);
    this.itemFilter = this.fn_group(this.activeTab);
  },
  methods: {
    fn_link: function(_value) {
      this.$router
        .push({
          path: "/product/" + this.group + "/" + this.model + "/" + _value
        })
        .catch(err => {
          console.log(err);
        });
    },
    fn_tab: function(_value) {
      this.activeTab = _value;
      this.itemFilter = this.fn_group(_value);
      this.selected = _value;
    },
    fn_select: function() {
      this.activeTab = this.selected;
      this.itemFilter = this.fn_group(this.selected);
    },
    fn_group: function(_value) {
      return this.items.filter(function(item) {
        return item.groupIdx === _value;
      });
    }
  },
  computed: {}
};
</script>>

